.TodoContainer{
    width: 100%;
    background-color: #fff;
    border-top: 1px solid #ccc;
    padding: 15px 80px;
    box-sizing: border-box;
}
.TodoContainer_title{
    width: 100%;
    border: 1px solid #09abff;
    font-size: 1.25rem;
    padding: 0.5rem;
    margin-bottom: 1rem;
    padding: 0.5rem;
    font-weight: 500;
    outline: none;
    box-sizing: border-box;
}
.TodoContainer_body{
    width: 100%;
    font-size: 0.875rem;
    min-height: 90px;
    padding: 0.5rem;
    border: 1px solid #09abff;
    outline: none;
    box-sizing: border-box;
}
.EmptyContainer h1{
    width: 100%;
    background-color: #fff;
    font-size: 1.25rem;
    padding: 15px 0;
    font-weight: 500;
}
.EmptyContainer p{
    width: 100%;
    background-color: #fff;
    font-size: 1.875rem;
    padding: 0.5rem;
    box-sizing: border-box;
}